'use strict';

import Timeago from 'timeago.js';
/**
 *   Date Date String
 *   (M)(d)(h)(m)(s)(q)  1-2 
 *   (y) 1-4 (S) 1 ( 1-3 )
 *   
 *   (new Date()).Format('yyyy-MM-dd hh:mm:ss.S') ==> 2006-07-02 08:09:04.423
 *   (new Date()).Format('yyyy-M-d h:m:s.S')      ==> 2006-7-2 8:9:4.18
 */
const fmtDate = (date, fmt) => { // author: meizz
    var o = {
        'M+': date.getMonth() + 1, // 
        'd+': date.getDate(), // 
        'h+': date.getHours(), // 
        'm+': date.getMinutes(), // 
        's+': date.getSeconds(), // 
        'q+': Math.floor((date.getMonth() + 3) / 3), // 
        'S': date.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    }
    for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
        }
    }
    return fmt;
};

/**
 * Timeago
 */
const MillisecondToDate = (time) => {
    var str = '';
    if (time !== null && time !== '') {
        let timeagoInstance = new Timeago();
        str = timeagoInstance.format(time, 'zh_CN');
    }
    return str;
};

let exports = {};

/**
 * 
 * @param {string} time 
 * @param {bool} friendly fromNow
 */
exports.getLastTimeStr = (time, friendly) => {
    if (friendly) {
        return MillisecondToDate(time);
    } else {
        return fmtDate(new Date(time), 'yyyy-MM-dd hh:mm');
    }
};

/**
 * tab
 * @param  {[type]}  tab     [tab]
 * @param  {[type]}  good    []
 * @param  {[type]}  top     []
 * @param  {Boolean} isClass []
 * @return {[type]}          []
 */
exports.getTabInfo = (tab, good, top, isClass) => {
    let str = '';
    let className = '';
    if (top) {
        str = '';
        className = 'top';
    } else if (good) {
        str = '';
        className = 'good';
    } else {
        switch (tab) {
            case 'share':
                str = '';
                className = 'share';
                break;
            case 'ask':
                str = '';
                className = 'ask';
                break;
            case 'job':
                str = '';
                className = 'job';
                break;
            default:
                str = '';
                className = 'default';
                break;
        }
    }
    return isClass ? className : str;
};

exports.fmtDate = fmtDate;
exports.MillisecondToDate = MillisecondToDate;

export default exports;


