 <template>
 	<div>
	    <h3></h3>
	    <div>
	    	<el-button class="primary">
				<input type="file" @change="fileChange">
	    	</el-button>
	    </div>
	    <div class="sourceImg">
	    	<img src="../../img/1491991034.png" alt="">
	    	<img src="../../img/cat.jpg"  alt="" ref='origin' v-if="!this.originImg">
	    	<img :src="originImg" alt="origin" v-else>
	    </div>
	    <h3>:</h3>
	    <div class="resultImg" ref="result"></div>
	    <div>
	    	<el-button type="primary" @click="download"></el-button>
	    </div>
    </div>
  </template>
<script>
	import QArt from 'qartjs';

	export default {
		data(){
			return {
				originImg: '',
				resultQR: ''
			}
		},
		methods:{
			fileChange(event){
				let files = event.target.files;
				let file = files[0];
				if(!file.type.match('image.*')){
					alert('');
					return;
				}
				let reader = new FileReader();
				reader.readAsDataURL(file);
				reader.onload = () => {
					this.generateQRcode(reader.result);
				}
			},
			generateQRcode(base64Img){
				this.originImg = base64Img;
				new QArt({
				  value: 'www.baidu.com',
				  imagePath: base64Img,
				  filter: 'color',
				  size: 195
				}).make(this.$refs.result);
			},
			download(){
				let resultCanvas = this.$refs.result.children[0];
				let type = "image/png";
				let image = resultCanvas.toDataURL(type).replace(type, "image/octet-stream");
				window.location.href = image;
			}
		},
		mounted(){
			let img = this.$refs.origin;
			this.generateQRcode(img.src);
		}
	}
</script>

<style lang="scss">
	.sourceImg,.resultImg{
		img{
			width: 100px;
			height: 100px;
		}
	}
	.primary {
		position: relative;
		input{
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			opacity: 0;
			width: 100%;
		}
	}
</style>


